import '../../../assets/tailwindcss/index.css'
  export default {
    name: '',

    props: {
      // 行索引
      rowIndex: {
        type: Number,
        default: -1
      },
      // 总行数。用于控制按钮是否显示
      rowCount: {
        type: Number,
        default: -1
      },
      // 行的label样式
      rowLabelClass: {
        type: String,
        default: ''
      },
      // 行的label
      rowLabel: {
        type: String,
        default: ''
      },
      // 是否显示增删行的按钮
      showOperationButton: {
        type: Boolean,
        default: false
      },
      // 校验提示
      errorTip: {
        type: String,
        default: ''
      },
      // 是否是必填项
      isRequired: {
        type: Boolean,
        default: false
      },
    },

    data() {
      return {

      };
    },

    methods: {
      // 添加行
      addRow() {
        this.$emit('addRow');
      },

      // 删除行
      removeRow() {
        this.$emit('removeRow');
      },
    }
  };
