import deepClone from "../../utils/deepClone";

export default {
  name: 'ak-select',

  inheritAttrs: false,

  props: {
    options: {
      type: Object,
      default: () => ({})
    }
  },

  watch: {
    options: {
      handler(newVal) {
        this.formData = deepClone(newVal);
      },
      deep: true,
      immediate: true
    }
  },

  data() {
    return {
      formData: {}
    };
  },

  methods: {
  }
};
