import Description from './description';
import EnumerateRadioItem from './EnumerateRadioItem';

export default {
  name: 'ExtentionRadio',
  components: {
    Description,
    EnumerateRadioItem
  },
  methods: {
    removeOption(keyArray) {
      this.$emit('removeOption', keyArray || []);
    },
    radioValueChange({ key, value }) {
      this.$emit('dataChange', { key, value });
    }
  },
  props: {
    option: {
      type: Object,
      default: () => ({})
    },
    defaultData: {
      type: [Boolean, String],
      default: '__empty__'
    }
  }
};
