import commonExtension from '../mixins/common-extension'
import ExtentionRowItem from '../extension-row-item';
import deepClone from '../../../utils/deepClone';

const ROW_ITEM = {
  key: '',
  value: ''
}

export default {
  name: 'DateShortcut',

  mixins: [commonExtension],

  components: {
    ExtentionRowItem
  },

  data() {
    return {
      formData: [deepClone(ROW_ITEM)]
    }
  },

  watch: {
    formData: {
      handler(newValue) {
        const newFormData = this.filterForm(newValue);
        if (newFormData.length === 0) {
          this.$emit('dataChange', { key: this.option.key, value: '' });
        } else {
          this.$emit('dataChange', { key: this.option.key, value: newFormData });
        }
      },
      deep: true
    },
  },

  methods: {
    filterForm(originData) {
      const data = deepClone(originData)
      for (let i = data.length - 1; i >= 0; i--) {
        const item = data[i]
        if (!item.key || !item.value) {
          data.splice(i, 1)
        }
      }
      return data
    },

    init() {
      if (!this.defaultData) { 
        return
      }
      this.formData = deepClone(this.defaultData)
    }
  },

  created() {
    this.init()
  }
}
