import deepClone from "../../utils/deepClone";

export default {
  name: 'ak-radio',

  inheritAttrs: false,

  props: {
    options: {
      type: Object,
      default: () => ({})
    }
  },

  watch: {
    options: {
      handler(newVal) {
        this.formData = deepClone(newVal);
      },
      deep: true,
      immediate: true
    }
  },

  data() {
    return {
      formData: {}
    };
  },

  methods: {
    radioChange(value) {
      const realValue = this.options.selectOptions.find(item => item.label === value);
      this.$emit('change', realValue);
    }
  }
};
